* {
  font-family: "Roboto", arial, sans-serif;
}

a {
  color: #167ac6;
  cursor: pointer;
  text-decoration: none;
}

.logo {
  background: no-repeat
    url(//www.gstatic.com/youtube/img/branding/youtubelogo/1x/youtubelogo_30.png);
  background-size: contain;
  width: 125px;
  height: 30px;
  cursor: pointer;
  display: inline-block;
}

.search {
  height: 32px;
}

.search .searchinput {
  border: 1px solid #ddd;
  border-right: 0;
  border-radius: 2px 0 0 2px;
  float: left;
  padding: 2px 6px;
  height: 26px;
  width: calc(100% - 80px);
  display: flex;
  align-items: center;
  background: #fff;
}

.search .searchinput:focus-within {
  border-color: #065fd4;
  border-right: 1px solid;
}

.search .searchinput input {
  border: 0;
  outline: 0;
  height: 24px;
  font-size: 16px;
  font-weight: 400;
  color: #000;
  width: 100%;
}

.search button {
  height: 32px;
  border: 1px solid #ddd;
  border-radius: 2px 0 0 2px;
  width: 65px;
  padding: 0px 6px;
  font-size: 20px;
  background: #fff;
  color: #888;
}

.search button:hover {
  border-color: #bbb;
  background: #eee;
  color: #000;
}

.context-menu {
  display: none !important;
  flex-direction: column;
  position: absolute;
  top: 56px;
  right: 20px;
  width: 250px;
  border: 1px solid #ddd;
  align-items: baseline !important;
  background: #fff;
}

.context-menu.show {
  display: flex !important;
}

.context-menu * {
  font-size: 16px !important;
  color: #666;
}

.context-menu .items {
  padding: 8px 0;
  width: 100%;
}

.context-menu .separator {
  height: 1px;
  width: 100%;
  background: #ddd;
}

.context-menu a {
  display: flex;
  align-items: center;
}

.context-menu a:hover {
  background: #ddd;
}

.context-menu a span {
  padding: 5px;
  font-size: 28px !important;
}
